import { GenericResponse } from "@/lib/api/baseApi";
import { placeholderData } from "@/types/base";
import { useQuery } from "@tanstack/react-query";

export type QueryKeyLiteral =
  | "users"
  | "teams"
  | "departments"
  | "roles"
  | "religions"
  | "employmentStatus"
  | "paymentMethods"
  | "paymentCycles"
  | "clients"
  | "clientTypes"
  | "currencies"
  | "fileTransferMethods"
  | "deadlines"
  | "jobTypes"
  | "jobStatuses"
  | "jobWorkflowStatuses"
  | "jobDeliveryTypes"
  | "profilePics"
  | "jobs";

interface QueryOptions<R> {
  queryKey: [QueryKeyLiteral, unknown?]; // e.g. ["jobs", filters]
  queryFn: () => Promise<GenericResponse<R>>;
  refetchOnWindowFocus?: boolean;
  refetchOnMount?: boolean;
}

const useSkillQuery = <R>({
  queryKey,
  queryFn,
  refetchOnMount = false,
  refetchOnWindowFocus = false,
}: QueryOptions<R>) => {
  return useQuery({
    queryKey: queryKey,
    queryFn: queryFn,
    placeholderData: placeholderData as GenericResponse<R>,
    refetchOnWindowFocus: refetchOnWindowFocus,
    refetchOnMount: refetchOnMount,
  });
};

export default useSkillQuery;
